import React, { Component } from 'react';
import 'jspsych/jspsych';
import 'jspsych/css/jspsych.css';
import 'jspsych/plugins/jspsych-html-keyboard-response';
import 'jspsych/plugins/jspsych-fullscreen';
import './jsPsychPlugins/ms-selection-response';
import { collectTrials, collectAssessments } from './experimentUtils';
import PointsDistribution from './PointsTracker';
import { 
  getTrialInstructions,
  getCue,
  getFixation,
  getTarget,
  getBlank1,
  getBlank2,
  getFeedback,
  getInitFullscreen,
  getEndFullscreen
} from './timelineDefintions';
import {
  getQ1,
  getQ2,
  getQ3,
  getQ4
} from './blockAssessmentItems';
import TrialTimings from './timings';

import './ExperimentBlock.css';    

const jsPsych = window.jsPsych;


class ExperimentBlock extends Component {
  pointsTracker = null;

  getTimeline() {
    const timeline = [];
    const { jitteredTimings, block, numTrials, blockIndex } = this.props;    
    this.pointsTracker = new PointsDistribution({
      total: 7.50,
      minValue: 0.05,
      maxValue: 0.55,
      numTrials: 25
    });
    const trialTimes = new TrialTimings({
      numTrials,
      jitteredTimings
    });

    
    if (block.init_fullscreen) {
      const init_fullscreen = getInitFullscreen();
      timeline.push(init_fullscreen)
    }

    const instructions = getTrialInstructions(block.condition, blockIndex);
    const cue = getCue(this.pointsTracker, trialTimes, block.condition);
    const fixation = getFixation(trialTimes);
    const target = getTarget(this.props.staircase);
    const blank1 = getBlank1();
    const feedback = getFeedback(trialTimes, block.condition, this.props.staircase);
    const blank2 = getBlank2(trialTimes, this.pointsTracker);

    cue.data.beginTrial = true;
    blank2.data.endTrial = true;
    const test_procedure = {
      timeline: [cue, fixation, target, blank1, feedback, blank2],
      repetitions: numTrials
    };

    timeline.push(instructions);

    if ("pre_block_assessment" in block) {
      timeline.push(getQ1(block.condition));
      timeline.push(getQ2(block));
    }
    
    timeline.push(test_procedure);

    if ("post_block_assessment" in block) {
      timeline.push(getQ3(block.condition, this.pointsTracker));
      timeline.push(getQ4(block));
    }

    if (block.end_fullscreen) {
      const end_fullscreen = getEndFullscreen();
      timeline.push(end_fullscreen)
    }

    return timeline;
  }

  componentDidMount() {
    this.initExperiment();
  }

  componentDidUpdate(prevProps) {
    if (this.props.condition !== prevProps.condition) {
      this.initExperiment()
    }
  }

  initExperiment() {
    jsPsych.init({
      timeline: this.getTimeline(),
      on_finish: this.onExperimentFinish.bind(this),
      display_element: 'jspsych-experiment'
    });
    this.experiment.focus();
  }

  onExperimentFinish() {
    const trialData = JSON.parse(
      jsPsych.data.get().json()
    );

    const trials = collectTrials(trialData);
    const successTrials = trials.filter(t => t.hit && !t.autolose);
    // console.log('trials: ', trials);
    const expData = {
      trial_data: trials,
      total_score: this.pointsTracker.currentTotal,
      success_rate: (successTrials.length / trials.length)
    }
    if ("post_block_assessment" in this.props.block) {
      expData.assessments = collectAssessments(trialData);
    }
    this.props.submitBlock(expData);
  }

  render() {
    console.log("block: ", this.props.block);
    return (
      <div id="jspsych-experiment" ref={(exp) => { this.experiment = exp }} >Experiment here</div>
    )
  }
}

export default ExperimentBlock;