import React, { Component } from 'react';

export default class Landing extends Component {

  state = {
    loadingError: false
  }

  componentDidMount() {
    const requiredParams = ['Social', 'Position', 'ID']

    const searchParams = new URLSearchParams(window.location.search);
    const urlParams = {};

    for (let p of searchParams) {
      urlParams[p[0]] = p[1];
    }
    
    const missingParams = requiredParams.filter(param => !(param in urlParams));

    if (missingParams.length) {
      this.setState({loadingError: `Invalid link - missing  url parameters: ${missingParams.join(", ")}`})
    }
    else {
      const { submitUrlParameters, finishStep } = this.props;
      submitUrlParameters(urlParams);
      finishStep();
    }
  }

  render() {
      if (this.state.loadingError) {
        return (
          <div className="container submittingData">
            <div className="vertical-center">
              <p>Loading Error:</p>
              <pre className="data-output">{JSON.stringify(this.state.loadingError, null, 2)}</pre>
            </div>            
          </div>
        )
      }

      return (
          <div className="container">
            <div className="vertical-center">
              <p>Link parameters:</p>
              <pre className="data-output">{JSON.stringify(this.state, null, 2)}</pre>
            </div>            
          </div>
      )

  }
}
