import React from 'react';
import intro2Image1Src from './instruction2-image1.png';
import intro3Image1Src from './instruction3-image1.png';

export const Intro1 = ({socialIssue, advanceStep}) => {
  const charityStatement = socialIssue.position > 0 ? socialIssue.position_statements.for_statement : socialIssue.position_statements.alternate_statement;
  const anticharityStatement = socialIssue.position > 0 ? socialIssue.position_statements.alternate_statement : socialIssue.position_statements.for_statement;
  
  return (
    <div>
      <h2 className="centered">
        Welcome to Stage 2 of the Study:<br />
        The HEID Task!
      </h2>
      <p><span className="bold">Estimated time:</span> 40 minutes, broken into 8 game rounds lasting 3-5 minutes each.</p>
      <p><span className="bold">In the next task, you will be playing a game to maximize:</span></p>
      <ol>
        <li><span className="bold">Winning Money for Self (2 Game Rounds):</span> Win up to 15 $ for yourself to spend on fun, relaxing activities, or anything you like.</li>
        <li><span className="bold">Increasing Donations to your Charity (2 Game Rounds):</span> Increase, by up to $15, how much money will be donated to a charity that fights FOR {charityStatement}.</li>
        <li><span className="bold">Decreasing Donations to your Anti-Charity  (2 Game Rounds):</span> Decrease, by up to $15, how much money will be donated to your anti-charity, a charity that fights AGAINST {charityStatement} and is FOR {anticharityStatement}.</li>
        <li><span className="bold">Winning Points (2 Game Rounds):</span> These rounds are not worth any money. They are an opportunity to practice and win for the sake of winning.</li>
      </ol>
      <p><span className="bold">The next page will provide specific instructions on what a game round looks like and what you need to do to win.</span></p>
      <p><span className="bold">At the end of this task, you will view how much money in total you won for yourself and your charity, as well as how much donation money you removed from your anti-charity. You will receive your compensation (money for yourself) immediately following your participation.  Charity donations will be provided at the end of this study (Date: July 01, 2019).</span></p>
      <div className="navButtons">
        <button className="nextButton" onClick={() => advanceStep()}>Next</button>
      </div>
    </div>
  );
}

export const Intro2 = ({advanceStep}) => {
  return (
    <div>
      <h4>
        <span className="bold underline">Overview of Each Game Round:</span>
      </h4>
      <div className="intro2image1"><img src={intro2Image1Src} alt="Task Instructions 1" /></div>
      <p></p>
      <ol>
        <li><span className="bold">Instructions:</span> At the start of each game-round (8 total), you will be instructed on what social issue you are fighting for and reminded of previous instructions.</li>
        <li><span className="bold">Questions:</span> You will be asked to answer a few questions about how you are feeling in the moment.</li>
        <li><span className="bold">Trials:</span> You will complete a number of trials where <span className="bold">your goal is to hit a target (grey circle appearing on your screen) as quickly as possible in each trial</span>. If you hit the target, you will win the value of that trial. If you miss the target, you will not win the value of that trial. Each trial is worth a different amount of money or points. More detailed instructions are provided in the next page.</li>
        <li><span className="bold">Questions:</span> At the end of each game-round, you will be asked some additional questions about how you are feeling.</li>
      </ol>
      <div className="navButtons">
        <button className="prevButton" onClick={() => advanceStep(true)}>Previous</button>
        <button className="nextButton" onClick={() => advanceStep()}>Next</button>
      </div>
    </div>
  );
}

