class TrialTimings {
  trials = []

  /*
- Cue: 1000 ms
- Fixation: 1.5 - 3s
- target: 150 ms -500 ms
- Blank1: 1s - target
- Feedback: 2 - 3s
- Blank2: 2s
*/

  constructor(settings) {
    this.trialIndex = 0;
    this.settings = settings;

    for (let i=0; i< settings.numTrials; i++) {
      const fixation = this.settings.jitteredTimings[i].fixation;
      const feedback = this.settings.jitteredTimings[i].feedback;
      
      this.trials.push({
        cue: 1000,
        fixation: fixation,
        feedback: feedback,
        blank2: 500
      });
    }
  }

  getCurrentTrial(section) {
    return this.trials[this.trialIndex][section];
  }

  incrementTrialIndex() {
    this.trialIndex++;
  }
}

export default TrialTimings;