import React from 'react';
import './Debriefing.css';


class Debriefing extends React.Component {
  state = {
    copied: false
  }

  constructor(props) {
    super(props);

    this.setTokenInClibboard = this.setTokenInClibboard.bind(this);
  }
  componentDidMount() {
    window.scrollTo(0, 0);
  }

  componentDidUpdate(prevProps, nextProps) {
    if (!prevProps.mturkToken && this.props.mturkToken) {
      this.setState({submitting: false});
    }
  }

  setTokenInClibboard() {
    const { mturkToken } = this.props;
    const el = document.createElement('textarea');
    el.value = mturkToken;
    document.body.appendChild(el);
    el.select();
    document.execCommand('copy');
    document.body.removeChild(el);
    
    this.setState({copied: true});
  }

  render() {
    const totals = {
      'self': 0,
      'charity': 0,
      'anti-charity': 0,
      'points': 0
    }
    this.props.expData && this.props.expData.forEach(block => {
      totals[block.condition] += block.final_points;
    });

    const { mturkToken } = this.props;
    return (
      <div className="debrief container">

        <div className="debrief-header">
            <h3>University of Toronto</h3>
            <hr />
            <span>Department of Psychology</span><br />
            <span>UNIVERSITY OF TORONTO, TORONTO, ONTARIO, CANADA M5S 3G3</span>
            <div className="header-copy">
              <p>Thank you for your participation in the study: Validation of the Hedonic and Eudaimonic Incentive Delay Task!</p>
              <p>Information about the study, your final earnings, and your unique MTurk participation code for this study will be displayed in the following debriefing information section.</p>
              <p>Please copy paste your MTurk participation code (displayed below in this debriefing section) to Mechanical Turk to redeem your final earnings for your participation. The additional money you earned during the HEID task will be added at a later date (within 5 business days).</p>
              <p>You might want to save your MTurk participation code and time/date of participation in case of potential errors.</p>
            </div>
        </div>

        
        <h4>
          Debriefing Information
        </h4>
        <p>
          Your responses will allow us to validate this task as a neuroimaging measure of â€œmotivation and positive emotionâ€ functioning, and examine in the future whether responses to pleasant and meaningful experiences influence the development and maintenance of depression. Overall, this task will contribute to the field of affective science and help us understand the depressed brain. This research is being supervised by a board certified and licensed clinical psychologist (Dr. Zindel Segal) and a leading research in Affective Neuroscience (Dr. Norman Farb). 
        </p>

        <h4>
        Total earnings
        </h4>
        <p>
          <span className="bold">Self:</span> ${totals['self'].toFixed(2)}<br />
          <span className="bold">Charity:</span> ${totals['charity'].toFixed(2)}<br />
          <span className="bold">Anti-Charity:</span> ${totals['anti-charity'].toFixed(2)}<br />
          <span className="bold">Points:</span> {totals['points'].toFixed(2)}<br />
        </p>

        <p>The total money earned for yourself will be added to your MTURK account at the end of your participation in this study. Charity donations will be provided at the closure of this study (Date: August 31, 2019). The total donated to each charity at the end of this study ultimately depend on your performance. The more you won in the Charity and Anti-Charity game rounds, the more likely your charity will win donation money and your anti-charity will not at the end of the study. </p>

        <h4>
          <span className="">Final Remarks</span>
        </h4>

        <p>We ask that you do not disclose any information about this experiment, especially anything on this debriefing form, to any of your peers or colleagues, as we do not want any prior knowledge of our participants to bias the results of our study. This can ultimately lead to the publication of non-credible data. We thank you in advance for your cooperation.</p>

        <p>Please note that your results will be de-identified and not tied to your personal information. Should you express any concern as a result of completing any part of the study, please contact: 
          &nbsp;<a rel="noopener noreferrer" href="mailto: eudaimonic.reward@gmail.com" target="_blank">eudaimonic.reward@gmail.com</a>. 
          If you have any further questions about the study, you may contact Dr. Norman Farb at 
          &nbsp;<a rel="noopener noreferrer" href="mailto: norman.farb@utoronto.ca" target="_blank">norman.farb@utoronto.ca</a> 
          or Le-Anh Dinh-Williams at 
          &nbsp;<a rel="noopener noreferrer" href="mailto: eudaimonic.reward@gmail.com" target="_blank">eudaimonic.reward@gmail.com</a>. 
          If you would like to receive updates on the study, please leave your email address in the text box and updates will be provided.
        </p>
        <p className="centered">
          <b>Thank you again for participating!</b>
        </p>

        <h4>
          MTurk Participation Code
        </h4>
        <p>
          Your participation code to redeem your final earnings should appear here. The additional money you earned during the HEID task will be added at a later date (within 5 business days). Email 
          &nbsp;<a rel="noopener noreferrer" href="mailto: eudaimonic.reward@gmail.com" target="_blank">eudaimonic.reward@gmail.com</a> if there are any issues regarding your participation:
        </p>

        <div className="debrief-submit">
          {mturkToken && (
            <p className="mturkToken">
              {mturkToken}
              <span 
                className={`copy-to-clipboard ` + (this.state.copied ? 'copied' : '')}
                onClick={this.setTokenInClibboard}
                >
                  { this.state.copied ? 'Copied' : 'Copy'}
                </span>
            </p>
          )}
        </div>

        <p className="bold italic">
          For more background information on the study, please read the following section:</p>
        <p>
          Depression is among the most common mental disorder, affecting an estimated 1 in 4 Canadians during their lifetime. Despite these rates, we still have a poor understanding of what psychological and biological processes may be promoting the development and maintenance of the disorder. One of the leading models of depression emphasizes that the way patients respond to a pleasant event - such that they are less likely to enjoy something typically viewed as enjoyable (e.g., eating, winning money) â€“ predicts the onset of symptoms and the likelihood of staying depressed for a longer period of time. Despite these findings, we still have an incomplete science of depression. We have yet to examine how depressed individuals respond to all types of positive experiences. Most of these studies are based on Hedonic rewards, which are positive events that are rewarding in the short-term because they provide pleasure and relaxation. No study to date has examined how individuals respond to long-term and more meaningful sources of positive emotions, termed Eudaimonic rewards. Examples include: feeling good when you do something you believe in (authenticity) or that is meaningful to you (e.g., succeeding at school/work). 
        </p>
        <p>
          In this study, you were asked to indicate a social issue that was important to you and complete game in which you fought to gain money for yourself (hedonic reward), to increase, by up to $15, how much money will be donated to a charity supporting the social issue of importance to you (eudaimonic reward), and to decrease, by up to $15, how much money will be donated to a charity that opposes the social issue of importance to you, similar to â€œfighting for good and against evilâ€ (eudaimonic reward). Your responses will allow us to validate this task as a measure of hedonic and eudaimonic rewards, and examine whether responses to pleasant and meaningful experiences influence the development and maintenance of depression. This task will contribute to the field of affective science and be adapted for neuroimaging studies to examine the depressed brain. 
        </p>
      </div>
    )
  }

}

export default Debriefing
