import React, { Component } from 'react';
import axios from 'axios';
import Landing from './Landing';
import QualityCheckSurvey from '../QualityCheckSurvey/QualityCheckSurvey';
import ErrorPage from '../ErrorPage/ErrorPage';
import BVTIntro from '../BVTIntro/BVTIntro';
import ExpIntro from '../ExpIntro/ExpIntro';
import ExperimentBlock from '../ExperimentBlock/ExperimentBlock';
import ExperimentManager from '../ExperimentManager/ExperimentManager';
import Debriefing from '../Debriefing/Debriefing';
import Spinner from '../Loading/Spinner';

import jitteredTimings from '../../settings/exp_jittered_timings.json';
import DBStaircase from '../ExperimentBlock/staircase';
import { getMeanForLast, shuffle } from '../ExperimentBlock/experimentUtils';
import issues from '../../settings/issues.json';

const MISSED_TRIAL_MODIFIER = 150;
const STUDY_ID = '101';
const FILEPATH = 'q_submissions';

class TaskManager extends Component {
  state = {
    steps: [],
    stepIndex: 0,
    practiceData: null,
    authentication: null,
    experimentData: null,
    qCheckSurveyData: null,
    submittingData: false,
    submittingError: false,
    noConsent: false,
    qUserID: null,
    urlParams: null,
    loadingError: false
  }

  defaultSettings = {
    "steps": [
        "landing",
        "BVTIntro",
        "practice",
        "ExpIntro",
        "experiment",
        "debriefing",
        "finished"
    ],
    "initialStepIndex": 0
  }
  practiceStaircase = null;

  constructor(props) {
    super(props);

    this.state.steps = this.defaultSettings.steps;
    this.state.stepIndex = this.defaultSettings.initialStepIndex;

    this.onPracticeSubmit = this.onPracticeSubmit.bind(this);
    this.advanceStep = this.advanceStep.bind(this);
    this.setIssue = this.setIssue.bind(this);
    this.experimentSubmit = this.experimentSubmit.bind(this);
    this.submitExpData = this.submitExpData.bind(this);
    this.beforeUnload = this.beforeUnload.bind(this);
    this.logAction = this.logAction.bind(this);
    this.submitUrlParameters = this.submitUrlParameters.bind(this);

    // temp for no SVT
    // this.socialIssue = this.getSocialIssue({title: 'Vegetarianism', position: -42});










    // this.state.experimentData = import from ./stubExpData
    this.state.allowUnload = false;










    this.state.allowPublicSubmit = true;
  }

  beforeUnload(e) {
    if (this.state.allowUnload) return;

    console.log("unoading!!!", e);
    e.preventDefault();
    e.returnValue = '';
  }

  componentDidMount() {
    window.addEventListener("beforeunload", this.beforeUnload);

    
  }

  setIssue(issue) {
    this.socialIssue = this.getSocialIssue(issue);
  }

  advanceStep() {
    if (this.state.stepIndex < this.state.steps.length - 1) {
      
      const { qUserID } = this.state;
      if (qUserID) {
        console.log("LOGGING!!!!!!!!")
        this.logAction({
          completingStep: this.currentStep()
        });
      }

      this.setState({stepIndex: this.state.stepIndex + 1});
    }
  }

  getSocialIssue(iss) {
    const socialIssue = issues.filter(i => i.title === iss.title).pop();

    const pos1 = iss.position.replace("FOR ", "")
    let position = 0;
    if (pos1 == socialIssue.position_statements.for_statement) {
      position = 1;
    }
    else if (pos1 == socialIssue.position_statements.alternate_statement) {
      position = -1;
    }
    else {
      throw Error(`No matching position statement for "${pos1}" and issue "${iss.title}" `)
    }

  
    const issue = {
      'title': socialIssue.title,
      'position': position,
      'position_statements': socialIssue.position_statements
    }
    return issue;
  }

  submitUrlParameters(urlParams) {
    // console.log("get url: ", {SocialIssue, Position});
    const socialIssue = this.getSocialIssue({
      title: urlParams.Social,
      position: urlParams.Position
    });

    this.setState({ 
      urlParams,
      socialIssue,
      qUserID: urlParams.ID
    }, async () => {
      const response = await this.qtrixStart()
      if (!("mturkID" in response.data)) {
        throw Error("no mturkID in response");
      }
      this.setState({mturkID: response.data.mturkID});
      this.logAction({ 
        message: 'successful landing',
        ...urlParams,
        mturkID: response.data.mturkID
      });
    });
  }

  onPracticeSubmit(practiceData) {
    // const staircaseValues = [...this.practiceStaircase.values];
    // last value unused
    // staircaseValues.pop();
    // practiceData.duration_calc1 = getMeanForLast(staircaseValues, 5);

    // let rt_sum = 0;
    // let rt_min = null;
    practiceData.rt_min = null;
    practiceData.trial_data.forEach(t => {
      
      if (!practiceData.rt_min || t.overall_rt < practiceData.rt_min) practiceData.rt_min = t.overall_rt;
    });
    // practiceData.duration_calc2 = rt_sum / practiceData.trial_data.length;
    // practiceData.duration_calc3 = Math.min(...staircaseValues);
    // practiceData.duration_calc4 = rt_min;
    this.setState({practiceData});

    // console.log("practiceData: ", practiceData);
    
    // // Advance to expInto.
    this.advanceStep();
  }

  experimentSubmit(expData) {
    console.log("Submitted exp with: ", expData);
    this.setState({experimentData: expData}, () => {
      this.submitExpData();

    });
  }


  async qtrixStart() {
    if (!this.state.qUserID) {
      console.log('no userID. Skipping qtrix start: ');
      return;
    }

    const DATA_API_URL = "https://6rn8zjuxgg.execute-api.us-east-1.amazonaws.com/dev/radlab-heid-qualtrix-start";
    const postData = {
      "study_id": STUDY_ID,
      "qUserID": this.state.qUserID
    }
    try {
      const response = await axios.post(DATA_API_URL, postData);
      return response;
    } catch(error) {
      const response = error.response || error;
      console.log("got error: ", response);
      return response; 
    }

  }

  async logAction(action) {
    if (!this.state.qUserID) {
      console.log('no qUserID. Skipping action: ', action);
      return;
    }

    const DATA_API_URL = "https://6rn8zjuxgg.execute-api.us-east-1.amazonaws.com/dev/qualtrix-log/";
    const postData = {
      "study_id": STUDY_ID,
      "action": {...action, timezoneOffset: new Date().getTimezoneOffset()},
      "qUserID": this.state.qUserID,
      "log_filename": `${FILEPATH}/{participantId}/participant-{participantId}-log.json`
    }
    try {
      const response = await axios.post(DATA_API_URL, postData);
      return response;
    } catch(error) {
      const response = error.response || error;
      console.log("got error: ", response);
      return response; 
    }

  }

  async submitData(postData) {
    this.setState({submittingData: true});
    // const DATA_API_URL = "https://6rn8zjuxgg.execute-api.us-east-1.amazonaws.com/dev/submit-pubdata/";
    const DATA_API_URL = "https://6rn8zjuxgg.execute-api.us-east-1.amazonaws.com/dev/radlab-heid-qualtrix-submit-data/";

    if (this.state.authentication || this.state.allowPublicSubmit) {
      if (!this.state.allowPublicSubmit) {
        axios.defaults.headers.common['Authorization'] = this.state.authentication.token;
      }
      try {
        const response = await axios.post(DATA_API_URL, postData);
        await this.setState({
          submittingData: false,
          submittingError: false
        });
        return response;
      } catch(error) {
        const response = error.response || error;
        await this.setState({
          submittingData: false,
          submittingError: response
        });
        return response;    
      }
    }
    else {
      console.log("No authentication. Not storing anything to server.");
    }
  }

  async submitExpData() {
    const trialData = []
    // const blockData = []

    this.state.experimentData.forEach(data => {
      data.block_trial_data.forEach(tdata => {
        tdata.trial_index = tdata.index;
        delete tdata.index;
        const block_data = JSON.parse(JSON.stringify(data))
        delete block_data.block_trial_data;
        trialData.push({...tdata, ...block_data});
      });
    });

    
    const postData = {
      submission: {
        'trial_data': {
          'type': 'bucket',
          'filename': '{participantId}/participant-{participantId}-trials.{timestamp}.csv',
          'filepath': 'q_submissions',
          'filetype': 'csv',
          'data': trialData
        }
      },
      qUserID: this.state.qUserID,
      study_id: STUDY_ID
    }

    console.log("about to submit", postData);
    const response = await this.submitData(postData);
    console.log("finished submitting data with: ", response);

    window.removeEventListener("beforeunload", this.beforeUnload);
  }
  
  currentStep() {
    return this.state.steps[this.state.stepIndex];
  }

  render() {

    if (this.state.submittingData) {
      return (
        <div className="container submittingData">
          <div className="vertical-center">
            <p>Please wait while your data is submitted.</p>
            <Spinner />
          </div>            
        </div>
      )
    }

    if (this.state.submittingError) {
      return (
        <div className="container submittingError">
          <div className="vertical-center">
            <p>There was an error and your data could not be submitted.</p>
            <pre>{JSON.stringify(this.state.submittingError, null, 2)}</pre>
          </div>
        </div>
      )
    }


    if (this.currentStep() === 'landing') {
      return (
        <Landing submitUrlParameters={this.submitUrlParameters} finishStep={this.advanceStep} />
      )
    }


    if (this.currentStep() === 'BVTIntro') {

      return (
        <div className="container copyPage">
          <BVTIntro socialIssue={this.state.socialIssue} completeIntro={this.advanceStep} />
        </div>
      );
    }

    if (this.currentStep() === 'practice') {
  

      this.practiceStaircase = new DBStaircase({
        firstVal: 400,
        down: 1,
        stepSizes: [4, 2, 1],
        verbosity: 1
      });
      return (
        <ExperimentBlock 
          submitBlock={this.onPracticeSubmit} 
          jitteredTimings={jitteredTimings}
          staircase={this.practiceStaircase}
          block={{condition: {type: 'practice'}}}
          numTrials={3}
          />
      );
    }

    if (this.currentStep() === 'ExpIntro') {
      return (
        <div className="container copyPage">
          <ExpIntro advanceStep={this.advanceStep} />
        </div>
      );
    }

    if (this.currentStep() === 'experiment') {
      let starting_duration = this.state.practiceData ? this.state.practiceData.rt_min : 400;
      return (
        <ExperimentManager
          starting_duration={starting_duration}
          socialIssue={this.state.socialIssue}
          onExperimentFinish={this.experimentSubmit}
          finishStep={this.advanceStep}
          logAction={this.logAction} 
          />
      );
    }

    if (this.currentStep() === 'debriefing') {
      return (
        <Debriefing
          expData={this.state.experimentData}
          debriefSubmit={this.debriefSubmit}
          mturkToken={this.state.mturkID}
        />
      );
    }

   
    return (
      <ErrorPage debugInfo={{
        msg: "No step matching current index.",
        steps: this.state.steps,
        showing: this.state.steps[this.state.stepIndex],
        index: this.state.stepIndex
      }} />
    );
    
  }

  showResults(data) {
    return (
      <div className="container">
        <h2>Results from this section:</h2>
        <p>
          <button onClick={this.advanceStep}>Continue</button>
        </p>
        
        <div className="showResults">
          <pre>{JSON.stringify(data, null, 2)}</pre>
        </div>
        
      </div>
    );
  }
}

export default TaskManager;