import React, { Component } from 'react';
import { Intro1, Intro2, Intro3 } from './BVTIntroCopy';
import './BVTIntro.css';


class BVTIntro extends Component {
  state = {
    steps: [
      'intro1',
      'intro2',
      'intro3'
    ],
    stepIndex: 0
  }

  constructor(props) {
    super(props);
    this.advanceStep = this.advanceStep.bind(this);
  }

  showing() {
    return this.state.steps[this.state.stepIndex];
  }

  advanceStep(reversed=false) {
    if (reversed) {
      if (this.state.stepIndex > 0) {
        this.setState({stepIndex: this.state.stepIndex - 1});
      }
      return;
    }

    if (this.state.stepIndex < this.state.steps.length - 1) {
      this.setState({stepIndex: this.state.stepIndex + 1});
    }
    else {
      this.props.completeIntro();
    }
  }

  render() {
    if (this.showing() === 'intro1') {
      return (
        <Intro1 socialIssue={this.props.socialIssue} advanceStep={this.advanceStep} />
      );
    }

    if (this.showing() === 'intro2') {
      return (
        <Intro2 socialIssue={this.props.socialIssue} advanceStep={this.advanceStep}  />
      );
    }

    if (this.showing() === 'intro3') {
      return (
        <Intro3 socialIssue={this.props.socialIssue} advanceStep={this.advanceStep}  />
      );
    }
    
  }
}

export default BVTIntro;