import { shuffle, randomFromInterval } from './experimentUtils';
import { range } from 'lodash';

class PointsDistribution {
  constructor(settings={}) {
    if (typeof settings.total !== "number") {
      throw new Error("No total specified for PointsTracker");
    }
    if (typeof settings.numTrials !== "number") {
      throw new Error("No numTrials specified for PointsTracker");
    }
    if (typeof settings.minValue !== "number") {
      throw new Error("No minValue specified for PointsTracker");
    }
    if (typeof settings.maxValue !== "number") {
      throw new Error("No maxValue specified for PointsTracker");
    }

    this.settings = settings;
    this.mean = settings.total / settings.numTrials;
    this.values = [];
    this.initValues();
    this.currentTotal = 0;
    this.currentIndex = 0;
  }

  getCurrentValue() {
    return this.values[this.currentIndex];
  }

  incrementIndex() {
    this.currentIndex += 1;
  }

  addValue(val) {
    this.currentTotal += val;
  }


  initValues() {
    const { numTrials, minValue, maxValue } = this.settings;
    const mean = Math.round(
      100 * (maxValue + minValue) / 2
    )/100;
    
    if (mean !== this.mean) {
      console.log('mean1: ', mean);
      console.log('mean2: ', this.mean);
      throw new Error("minValue and maxValue settings are inconsistent with total and numTrials settings for PointsTracker");
    }
    const values = [mean];

    const deltas = range(0.05, mean, 0.05);
    const numPairs = (numTrials - 1)/2;

    for (let i=0; i<numPairs; i++) {
      let delta = deltas[randomFromInterval(0, deltas.length)];
      delta = Math.round(delta * 100)/100;

      const newLower = Math.round((mean - delta) * 100)/100;
      const newUpper = Math.round((mean + delta) * 100)/100;
      values.push(newLower);
      values.push(newUpper);
    }
    this.values = shuffle(values);
  }
}

export default PointsDistribution;
