class KeyLogger {
  keyLog = []
  logger = (event) => {
    // only check for spacebar
    if (event.keyCode === 32) {
      this.keyLog.push(event.keyCode);
    }
  }
}

const shuffle = function(arr) {
  var currentIndex = arr.length;
  var randIndex, tempValue;

  while (currentIndex !== 0) {
    randIndex = Math.floor(Math.random() * currentIndex);
    currentIndex--;

    tempValue = arr[currentIndex];
    arr[currentIndex] = arr[randIndex];
    arr[randIndex] = tempValue;
  }
  return arr;
}

const getMeanForLast = (arr, n) => {
  if (arr.length < n) {
    n = arr.length;
  }
  var data = arr.slice(-n);
  var sum = 0;
  for (var i = 0; i < data.length; i++) {
    sum += data[i];
  }
  return Math.round(sum / n);
}

const randomFromInterval = (min, max, step=1) => {
  const range = (max - min) / step
  return min + Math.floor(Math.random() * range) * step
}

function logTrialTimings(currentTrial) {
  currentTrial.total_duration = currentTrial.jspsych_timestamps.blank2_end_time_elapsed - currentTrial.absolute_trial_start;
  for (let key in currentTrial.jspsych_timestamps) {
    currentTrial[key] = currentTrial.jspsych_timestamps[key];
  }
  delete currentTrial.jspsych_timestamps;
  return currentTrial;
}

function logTrialResponses(currentTrial) {
  for (let key in currentTrial.response_logs) {
    currentTrial[`${key}_num_responses`] = currentTrial.response_logs[key];
  }
  delete currentTrial.response_logs;
  return currentTrial;
}

const collectTrials = (trialData) => {
  const trials = []
  let currentTrial = null
  let trialIndex = 1
  let cumulativePointsOnOffer = 0;
  let cumulativePointsGained = 0;
  trialData.forEach( (trialPart) => {
      delete trialPart.stimulus

      if (trialPart.beginTrial) {
          currentTrial = {
            index: trialIndex++,
            jspsych_timestamps: {},
            response_logs: {
              fixation: 0,
              target: 0,
              blank1: 0
            },
            absolute_trial_start: trialPart.absolute_trial_start
          }
      }

      [
        'cue',
        'fixation',
        'target',
        'blank1',
        'feedback',
        'blank2'
      ].forEach((n) => {
        if (trialPart[n]) {
          currentTrial.jspsych_timestamps[`${n}_start_time_elapsed`] = trialPart.start_time_elapsed;
          currentTrial.jspsych_timestamps[`${n}_end_time_elapsed`] = trialPart.time_elapsed;

          if (Object.keys(trialPart).includes('keylog')) {
            currentTrial.response_logs[n] += trialPart.keylog.length;
          }
        }
      });

      
      if (trialPart.target) {
        currentTrial.hit = trialPart.hit;
        currentTrial.response_logs.target += currentTrial.hit ? 1 : 0;
        currentTrial.target_presentation_rt = trialPart.rt;
        currentTrial.target_presentation_duration = trialPart.presentation_duration;
      }

      if (trialPart.blank1) {
        currentTrial.overall_rt = currentTrial.target_presentation_rt;
        if (!currentTrial.overall_rt && trialPart.rt) {
          currentTrial.overall_rt = currentTrial.target_presentation_duration + trialPart.rt;
        }
      }

      if (trialPart.point_value) {
        currentTrial.point_value = trialPart.point_value;
        cumulativePointsOnOffer += trialPart.point_value;
        currentTrial.cumulativePointsOnOffer = cumulativePointsOnOffer;
      }

      if (trialPart.feedback) {
        currentTrial.autolose = trialPart.autolose;
      }

      if (trialPart.endTrial) {
        if (currentTrial.hit && !currentTrial.autolose) {
          cumulativePointsGained += currentTrial.point_value;
        }
        currentTrial.cumulativePointsGained = cumulativePointsGained;
        logTrialTimings(currentTrial);
        logTrialResponses(currentTrial);
        trials.push(currentTrial);
    }
  })

  return trials
}

function collectAssessments(trialData) {
  const assessments = {};
  trialData.forEach( (trialPart) => {
    [
      "q1",
      "q2_a1",
      "q2_a2",
      "q2_b1",
      "q2_b2",
      "q3",
      "q4_1",
      "q4_2",
      "q4_3",
      "q4_4",
    ].forEach((n) => {
      if (trialPart[n]) {
        assessments[`${n}_time_elapsed`] = trialPart.time_elapsed;
        assessments[`${n}_response`] = trialPart.response;
        if (n.includes('a')) {
          assessments[`${n.replace('a', 'b')}_response`] = null;
          assessments[`${n.replace('a', 'b')}_time_elapsed`] = null;
        }
        else if (n.includes('b')) {
          assessments[`${n.replace('b', 'a')}_response`] = null;
          assessments[`${n.replace('b', 'a')}_time_elapsed`] = null;
        }
      }
    })

  });
  return assessments;
}

function collectDebrief(expData) {
  console.log('collectDebrief: ', expData);
  // blocks[this.state.blockIndex].final_points = blockData.total_score;
  //   blocks[this.state.blockIndex].final_duration = finalDuration;


  const results = {
    num_blocks: expData.length,
    block_data: expData.map(block => {
      return {
        block_index: block.block_index,
        condition: block.condition,
        final_points: block.final_points.toFixed(2),
        success_rate: block.success_rate
      }
    })
  }
  return results;
}


export {
  KeyLogger,
  randomFromInterval,
  shuffle,
  collectTrials,
  collectAssessments,
  getMeanForLast,
  collectDebrief
}
