import React, { Component } from 'react';
import ExperimentBlock from '../ExperimentBlock/ExperimentBlock';
import { shuffle, randomFromInterval } from '../ExperimentBlock/experimentUtils';
import DBStaircase from '../ExperimentBlock/staircase';
import jitteredTimings from '../../settings/exp_jittered_timings.json';

const NUM_TRIALS = 3;
const FULLSCREEN = true;

class ExperimentManager extends Component {
  state = {
    blocks: [],
    blockIndex: 0,
    tempResults: null
  }
  staircase = null;

  constructor(props) {
    super(props);
    // this.state.blocks = this.randomizeBlocks();
    /////////////////////////////////////////////
    // FOR DEBUGGING
    //
    // this.state.blocks = this.randomizeBlocks().slice(0,2);
    const blocks = this.randomizeBlocks();
    console.log("BLOCKS: ", blocks);
    this.state.blocks = [blocks[0], blocks[7]];

    this.onBlockSubmit = this.onBlockSubmit.bind(this);
  }

  advanceBlocks() {
    if (this.state.blockIndex < this.state.blocks.length - 1) {
      this.props.logAction({
        completingStep: "BVT - blockIndex: " + (this.state.blockIndex + 1)
      });
      this.setState({blockIndex: this.state.blockIndex + 1});
    }
    else {
      const data = this.state.blocks.map((b, i) => this.collectBlock(b, i));
      this.setState({tempResults: data}, () => {
        this.props.onExperimentFinish(data);
        this.props.finishStep();
      });
    }
  }


  collectBlock(blockData, i) {
    blockData.block_index = i + 1;
    blockData.social_issue = blockData.condition.socialIssue ? blockData.condition.socialIssue.title : null;
    blockData.social_issue_position = blockData.condition.socialIssue ? blockData.condition.socialIssue.position : null;
    blockData.condition = blockData.condition.type;

    for (let key in blockData.assessments) {
      blockData[key] = blockData.assessments[key];
    }
    ["init_fullscreen", "end_fullscreen"].forEach(name => {
      if (name in blockData) {
        delete blockData[name];
      }
    });
    
    delete blockData.assessments;
    delete blockData.pre_block_assessment;
    delete blockData.post_block_assessment;
    return blockData;
  }


  onBlockSubmit(blockData) {
    const blocks = [...this.state.blocks];
    const finalDuration = blockData.trial_data[blockData.trial_data.length - 1].target_presentation_duration;
    blocks[this.state.blockIndex].final_points = blockData.total_score;
    blocks[this.state.blockIndex].final_duration = finalDuration;
    blocks[this.state.blockIndex].block_trial_data = blockData.trial_data;
    blocks[this.state.blockIndex].success_rate = blockData.success_rate;
    blocks[this.state.blockIndex].assessments = blockData.assessments;

    // set next block starting_duration;
    if (this.state.blockIndex < blocks.length - 1) {
      blocks[this.state.blockIndex + 1].starting_duration = this.staircase.values.pop();
    }

    this.setState({blocks});
    this.advanceBlocks();
  }

  getBlockAssessments(condition, blocks) {
    const prevAssessment = blocks.filter(b => b.condition.type === condition.type).pop();
    
    let intra_condition_1;
    if (prevAssessment) {
      intra_condition_1 = Object.keys(prevAssessment.pre_block_assessment).includes('q2_b1') ? 'a' : 'b';
    } else {
      intra_condition_1 = randomFromInterval(0, 2) ? 'a' : 'b';
    }
    let pre_block = {q1: null};
    let post_block = {q3: null};
    [1,2].forEach(n => {pre_block[`q2_${intra_condition_1}${n}`] = null});
    [1,2,3,4].forEach(n => {post_block[`q4_${n}`] = null});

    return {
      pre_block,
      post_block
    };
  }

  randomizeBlocks() { // todo: rename to initBlocks
    console.log("socialIssue: ", this.props.socialIssue);
    // throw Error("randomizeBlocks")
    let conditions = shuffle(['self', 'anti-charity', 'charity', 'points']);
    let newConditions = shuffle(['self', 'anti-charity', 'charity', 'points']);
    while (newConditions[0] === conditions[conditions.length - 1]) {
      newConditions = shuffle(newConditions);
    }
    conditions = conditions.concat(newConditions);

    let blocks = [];
    conditions.forEach(c => {
      let condition = {
        type: c,
        socialIssue: ['charity', 'anti-charity'].includes(c) ? this.props.socialIssue : null
      };

      const assessments = this.getBlockAssessments(condition, blocks);
      blocks.push({
        starting_duration: null,
        condition: condition,
        final_duration: null,
        block_trial_data: {},
        final_points: null,
        pre_block_assessment: assessments.pre_block,
        post_block_assessment: assessments.post_block,
      });
    });
    blocks[0].starting_duration = this.props.starting_duration;

    if (FULLSCREEN) {
      blocks[0].init_fullscreen = true;
      blocks[blocks.length - 1].end_fullscreen = true;
    }
    return blocks;
  }

  render() {
    console.log('blocks: ', this.state.blocks);

    if (this.state.tempResults) {
      return (<div><pre>{JSON.stringify(this.state.tempResults, null, 2)}</pre></div>);
    }
    
    this.staircase = new DBStaircase({
      firstVal: this.state.blocks[this.state.blockIndex].starting_duration,
      down: 2,
      stepSizes: [2, 2, 1],
      verbosity: 1
    });

    return (
      <div>
        <ExperimentBlock
          submitBlock={this.onBlockSubmit} 
          jitteredTimings={jitteredTimings}
          staircase={this.staircase}
          block={this.state.blocks[this.state.blockIndex]}
          condition={this.state.blocks[this.state.blockIndex].condition}
          numTrials={NUM_TRIALS}
          blockIndex={this.state.blockIndex}
        />
      </div>
    );
  }
}

export default ExperimentManager;
