import React from 'react'
import RadioRange from '../WBManager/components/RadioRange/RadioRange';
import './QualityCheckSurvey.css';

const Legend = () => (
  <div className="form-legend">
    <div>Strongly Agree</div>
    <div>Strongly Disagree</div>
  </div>
);

const EngagementLegend = () => (
  <div className="form-legend">
    <div>Not at all</div>
    <div>Very much</div>
  </div>
);

class QualityCheckSurvey extends React.Component {
  state = {
    qcheck_1: null,
    qcheck_2: '',
    qcheck_3a: '',
    qcheck_3b: '',
    qcheck_4: '',
    qcheck_email: '',
  }

  constructor(props) {
    super(props);

    this.handleFieldChange = this.handleFieldChange.bind(this);
    this.handleFormSubmit = this.handleFormSubmit.bind(this);
    this.canSubmit = this.canSubmit.bind(this);
  }

  canSubmit() {
    const canSubmit = this.state.qcheck_1 !== null && this.state.qcheck_3a !== '' && this.state.qcheck_3b !== '';
    return canSubmit;
  }

  handleFieldChange(e) {
    this.setState({[e.target.name]: e.target.value});
  }

  handleFormSubmit(e) {
    e.preventDefault();
    this.props.onQCheckSurveyFinish({...this.state});
  }

  render() {
    return (
      <div className="QualityCheckSurvey">
        <h2 className="centered">Quality Check Survey</h2>
        <div className="inner-wrapper">
            <p>Please answer the following questions regarding your experience completing the HEID task (i.e., the last task you just completed to win money for Self, Charity, Anti-Charity, and Points).</p>
            <br />
            <form 
                  onSubmit={this.handleFormSubmit}
                >

              <div className="qcheck_1">
                <p>1) The instructions provided in this study were clear, and I understood what I was being asked to do?</p>
                <div>
                  <Legend />
                  <RadioRange
                    handleRadioChange={this.handleFieldChange}
                    currentValue={this.state.qcheck_1}
                    name={'qcheck_1'}
                    rangeMin={1}
                    rangeMax={5}
                  />
                </div>
              </div>

              <div className="qcheck_2">
                <p>2) In one sentence or two, what were you asked to do in the HEID task (Stage 2 Task â€“ HEID Task)?</p>
                <p>
                  <textarea 
                    className=""
                    name="qcheck_2"
                    value={this.state.qcheck_2} 
                    onChange={this.handleFieldChange} 
                    maxLength={500} />
                </p>
              </div>

              <div className="qcheck_3">
                <p>3) Rate how intense the following emotions were - in general - when completing the task:</p>

                <h4 className="qcheck_header">I Felt Bored</h4>
                <div className="qcheck_3a">
                  <div>
                    <EngagementLegend />
                    <RadioRange
                      handleRadioChange={this.handleFieldChange}
                      currentValue={this.state.qcheck_3a}
                      name={'qcheck_3a'}
                      rangeMin={0}
                      rangeMax={9}
                    />
                  </div>

                </div>
                 
                 
                <h4 className="qcheck_header">I Felt Engaged</h4>
                    <div className="qcheck_3b">
                      <EngagementLegend />
                      <RadioRange
                        handleRadioChange={this.handleFieldChange}
                        currentValue={this.state.qcheck_3b}
                        name={'qcheck_3b'}
                        rangeMin={0}
                        rangeMax={9}
                      />
                    </div>
                

              </div>

              <div className="qcheck_4">
                <p>4) Please write any feedback you have about the task and/or your performance. It can be anything (e.g., anything that was hard to see or understand? Did you stop paying attention?). We are curious to hear about your experience and your feedback will help improve future versions of the task. We would rather know if something went wrong so we can include it in the data.</p>
                <p>
                  <textarea 
                    className=""
                    name="qcheck_4"
                    value={this.state.qcheck_4} 
                    onChange={this.handleFieldChange} 
                    maxLength={500} />
                </p>
              </div>

              <hr />

              <div className="qcheck_email">
                <p>Thank You.</p>
                <p>Enter your email if you wish for updates on the study:</p>
                <p>
                  <input
                    className=""
                    name="qcheck_email"
                    value={this.state.qcheck_email} 
                    onChange={this.handleFieldChange}
                    placeholder={'Your Email (Optional)'}
                    />
                </p>
              </div>

              <p style={{textAlign: 'right'}}>
                <button
                  className="btn btn-primary"
                  disabled={!this.canSubmit()}
                >Finish</button>
              </p>
            </form>
        </div>
      </div>
    )
  }

}

export default QualityCheckSurvey
