
function getQ1(condition) {
  const q1 = {
    type: "ms-selection-response",
    prompt: () => {
      const conditionCopy = {
        'charity': "win money for your charity?",
        'anti-charity': "win against your anti-charity?",
        'self': "win money for yourself?",
        'points': "win points?",
      }
      return `How motivated are you right now to ${conditionCopy[condition.type]}`;
    },
    labels: ['Not at all', '', 'Somewhat', '', 'Very much'],
    data: {q1: true},
    on_finish: function(data) {
      data.response = data.selection;
    }
  };

  return q1;
}

function getQ2(block) {
  const timeline = [];

  const conditionCopy = {
    'charity': "winning for your charity",
    'anti-charity': "winning against your anti-charity",
    'self': "winning money for yourself",
    'points': "winning points",
  }
  const q2questions = {
    "q2_a1": "do something relaxing or pleasant?",
    "q2_a2": "do what you believe in?",
    "q2_b1": "pursue a personal ideal?",
    "q2_b2": "do something enjoyable?"
  };

  // add question for each entry in block.pre_block_assessment
  Object.keys(block.pre_block_assessment).forEach(k => {
    if (k !== 'q1') {
      timeline.push({
        type: "ms-selection-response",
        prompt: () => {
          return (
            `<p>To what degree will ${conditionCopy[block.condition.type]} help you</p>` +
            `<p>${q2questions[k]}</p>`
          );
        },
        labels: ['Not at all', '', 'Somewhat', '', 'Very much'],
        data: {[k]: true},
        on_finish: function(data) {
          data.response = data.selection;
        }
      });
    }
    
  });

  timeline.push({
    type: "html-keyboard-response",
    stimulus: () => (
      `<div class="halt">` +
        `<p>Press any key when you are ready to start the task.</p>` +
      `</div>`
    ),
    data: { halt: true }
  });
  return { timeline };
}

function getQ3(condition, pointsTracker) {
  const q3 = {
    type: "ms-selection-response",
    prompt: () => {
      let feedback;
      const amount = pointsTracker.currentTotal.toFixed(2);
      switch (condition.type) {
        case 'self':
          feedback = `In this block, you earned $${amount} for yourself.`
          break;
        case 'points':
          feedback = `In this block, you earned ${amount} points.`
          break;
        case 'charity':
          feedback = `In this block, you earned $${amount} for your charity.`
          break;
        case 'anti-charity':
          feedback = `In this block, you removed $${amount} from your anti-charity.`
          break;
        default:
          break;
      }

      return (
        `<p>${feedback}</p>` +
        `<p>How satisfied are you with this outcome?</p>`
      );
    },
    labels: ['Not at all', '', 'Somewhat', '', 'Very much'],
    data: {q3: true},
    on_finish: function(data) {
      data.response = data.selection;
    }
  };

  return q3;
}

function getQ4(block) {
  const timeline = [];

  const q4questions = {
    "q4_1": "pleased / happy",
    "q4_2": "that this activity was meaningful / valuable",
    "q4_3": "relaxed",
    "q4_4": "that I am acting according to my values"
  };

  // add question for each entry in block.pre_block_assessment
  Object.keys(block.post_block_assessment).forEach(k => {
    if (k !== 'q3') {
      timeline.push({
        type: "ms-selection-response",
        prompt: () => {
          return (
            `<p>Please rate the extent to which you feel the following at the present moment:</p>` +
            `<p>I feel ${q4questions[k]}</p>`
            );
        },
        labels: ['Not at all', '', 'Somewhat', '', 'Very much'],
        data: {[k]: true},
        on_finish: function(data) {
          data.response = data.selection;
        }
      });
    }
    
  })
  return { timeline };
}


export {
  getQ1,
  getQ2,
  getQ3,
  getQ4
}