import { KeyLogger } from './experimentUtils';

const jsPsych = window.jsPsych;
const checkMark = `âœ”`;
const xMark = `âœ˜`;

function instructionsCopy({type, socialIssue}) {
  let charityStatement = '';
  let anticharityStatement = '';
  if (socialIssue) {
    charityStatement = socialIssue.position > 0 ? socialIssue.position_statements.for_statement : socialIssue.position_statements.alternate_statement;
    anticharityStatement = socialIssue.position > 0 ? socialIssue.position_statements.alternate_statement : socialIssue.position_statements.for_statement;
  }
  
  const copy = {
    "practice": [
      `<span class="bold">Now you will get the chance to <span class="italic">just</span> practice the task.</span>`,
      `Press SPACEBAR when you see the grey circle target as quickly as possible before it disappears.`,
      `<span class="bold underline">Remember:</span> Hit the SPACEBAR ONLY <span class="bold underline">after the target appears</span> and <span class="bold underline">before it disappears</span>. You will also lose the trial if you press the SPACEBAR <span class="bold underline">more than ONCE</span> and <span class="bold underline">before the target appears</span>.`
    ],
    "points": [
      `In this round, your goal is to <span class="bold">win points</span>.`,
      `There is no money at stake in this round. It can be fun just to win points.`,
      `Each time you respond quickly enough, you <span class="bold">add the value of that trial (e.g., WIN! $ 0.5 ${checkMark}) to your total point score (e.g., WIN! pts 0.5 ${checkMark})</span>.`,
      `If you are too slow, nothing is added to your total point score <span class="bold">(e.g., LOSE! pts 0.5 ${xMark})</span>`,
      `The more you win, the more points you win for yourself!`
      ],
    "charity": [
      `In this round, your goal is to <span class="bold">win FOR ${charityStatement}</span> and increase donations to this issue.`,
      `Each time you respond quickly enough, <span class="bold">you add the value of that trial (e.g., WIN! $ 0.5 ${checkMark}) to the donation fund of your charity</span> - a charity that fights FOR ${charityStatement}.`,
      `If you are too slow, nothing is added to the donation fund of your charity (e.g., LOSE! $ 0.5 ${xMark}).`,
      `<span class="bold">The more you win, the more money you win for your charity!</span>`
      ],
    "anti-charity": [
      `In this round, your goal is to <span class="bold">win AGAINST ${anticharityStatement}</span> and decrease donations to this issue.`,
      `Each time you respond quickly enough, <span class="bold">you remove the value of that trial (e.g., WIN! $ 0.5 ${checkMark}) from the donation fund of your anti-charity</span> - a charity that fights FOR ${anticharityStatement}.`,
      `If you are too slow, nothing is removed from the donation fund of your anti-charity (e.g., LOSE! $ 0.5 ${xMark}).`,
      `<span class="bold">The more you win, the more money you remove from your anti-charity!</span>`,
      `Each time you respond quickly enough, you remove the value of that trial (e.g., WIN! $ 0.5 âœ”) from the donation fund of your anti-charity - a charity that fights <b>FOR ${anticharityStatement}</b> and <b>AGAINST ${charityStatement}</b>.`
      ],
    "self": [
      `In this round, your goal is to <span class="bold">win</span> money for <span class="bold">yourself</span></b>.`,
      `Each time you respond quickly enough, <span class="bold">you add the value of that trial (e.g., WIN! $ 0.5 ${checkMark}) to your total earnings</span>.`,
      `If you are too slow, nothing is added to your total earnings (e.g., LOSE! $ 0.5 ${xMark}).`,
      `<span class="bold">The more you win, the more money you win for yourself!</span>`
    ]
  };

  return copy[type];
}

function getProtestorSign(signPos, type, dollar, points) {
  let out = `<div class="protestor icon"><div class="protest-sign">`;
  if (points) {
    const signPoints = dollar ? `$ ${points}` : `${points}`;
    out += `<div class="protest-sign-points">${signPoints}</div>`
  }
  out += `<div><span class="protest-sign-position">${signPos}</span> ${type.toUpperCase()}</div>`;
  out += `</div></div>`;
  return out;
}

function getTrialInstructions(condition, blockIndex) {
  return {
    type: "html-keyboard-response",
    data: { instructions: true },
    stimulus: () => {
      let copy = ``;
      instructionsCopy(condition).forEach(para => {
        copy += `<p>${para}</p>`;
      });
      const signPos = condition.type === 'anti-charity' ? `AGAINST` : `FOR`;
      const blockIndexFeedback = condition.type === 'practice' ? `` : `<div class="blockIndex"><small>${blockIndex + 1} / 8</small></div>`;
      return (
        `<div class="intructions-response">` +
          `<div class="instructions ${condition.type}">` +
            getProtestorSign(signPos, condition.type) + 
            blockIndexFeedback +
            `<div class="copy">${copy}</div>` +
          `</div>` +
          `<div><p>Press any key to begin...</p></div>` +
        `</div>`
      );
    }
  }
}

function getCue(pointsTracker, trialTimes, condition) {
  const cue = {
    type: "html-keyboard-response",
    stimulus: () => {
      const signPos = condition.type === 'anti-charity' ? `AGAINST` : `FOR`;
      const pointVal = pointsTracker.getCurrentValue().toFixed(2);
      return (
        `<div class="cue">` +
          getProtestorSign(signPos, condition.type, (condition.type !== 'points'), pointVal) +
        `</div>`
      );
    },
    data: { cue: true },
    on_start: ({data}) => {
      data.start_time_elapsed = jsPsych.totalTime();
      data.absolute_trial_start = jsPsych.totalTime();
    },
    trial_duration: () => trialTimes.getCurrentTrial('cue'),
    response_ends_trial: false,
    on_finish: (data) => {
      data.point_value = pointsTracker.getCurrentValue();
      data.presentation_duration = trialTimes.getCurrentTrial('cue');
    }
  };
  return cue;
}

function getFixation(trialTimes) {
  let keyLogger = null;
  const   fixation = {
    type: "html-keyboard-response",
    stimulus: '<div style="font-size: 60px;">+</div>',
    response_ends_trial: false,
    data: {fixation: true},
    on_start: ({data}) => {
      keyLogger = new KeyLogger();
      document.addEventListener("keyup", keyLogger.logger);

      // log start
      data.start_time_elapsed = jsPsych.totalTime();
    },
    trial_duration: () => trialTimes.getCurrentTrial('fixation'),
    on_finish: function(data) {
      data.keylog = keyLogger.keyLog;
      data.presentation_duration = this.trial_duration;

      document.removeEventListener("keyup", keyLogger.logger);
    }
  }

  return fixation;
}

function getTarget(staircase) {
  const target = {
    type: "html-keyboard-response",
    stimulus: '<div style="display: block; height: 80px; width: 80px; background: #666; border-radius: 50%;"></div>',
    choices: ['Space'],
    data: {target: true},
    on_start: ({data}) => {
      data.start_time_elapsed = jsPsych.totalTime();
    },
    trial_duration: () => { return staircase.getValue(); },
    on_finish: function(data) {
      data.hit = data.rt ? true : false;
      data.presentation_duration = this.trial_duration;
      // staircase.addResponse(data.hit);
      // if (staircase.stairs.numReversals >= NUM_REVERSALS) {
      //   jsPsych.endExperiment("Hit max reversals: ", staircase.stairs.numReversals)
      //   closeFullscreen()
      // }
    }
  }
  return target;
}


function getBlank1(trialTimes) {
  let keyLogger = null;
  const blank1 = {
    type: "html-keyboard-response",
    stimulus: '<div></div>',
    response_ends_trial: false,
    data: {blank1: true},
    on_start: ({data}) => {
      keyLogger = new KeyLogger();
      document.addEventListener("keyup", keyLogger.logger);

      // log start
      data.start_time_elapsed = jsPsych.totalTime();
    },
    trial_duration: () => {
      const timelineData = jsPsych.data.getLastTimelineData();
      const targetData = JSON.parse(
        timelineData.filter({target: true}).json()
      ).pop();
      if (targetData.rt) {
        return 1000 - parseInt(targetData.rt);
      }
      else {
        return 1000 - targetData.presentation_duration;
      }
    },
    on_finish: function(data) {
      data.presentation_duration = this.trial_duration;
      const timelineData = jsPsych.data.getLastTimelineData();
      const targetData = JSON.parse(
        timelineData.filter({target: true}).json()
      ).pop();
      data.keylog = targetData.rt ? keyLogger.keyLog.slice(1) : keyLogger.keyLog;

      document.removeEventListener("keyup", keyLogger.logger);
    }
  }

  return blank1;
}

function getFeedback(trialTimes, condition, staircase) {
  const feedback = {
    type: "html-keyboard-response",
    stimulus: () => {
      const timelineData = jsPsych.data.getLastTimelineData();

      const cueData = JSON.parse(
        timelineData.filter({cue: true}).json()
      ).pop();
      const fixationData = JSON.parse(
        timelineData.filter({fixation: true}).json()
      ).pop();
      const targetData = JSON.parse(
        timelineData.filter({target: true}).json()
      ).pop();
      const blank1Data = JSON.parse(
        timelineData.filter({blank1: true}).json()
      ).pop();
      
      // DETECT CHEATING
      let autolose = false;
      const noResponse = (!fixationData.rt && !targetData.rt && !blank1Data.rt)
      const doubleTap = (targetData.rt && blank1Data.rt);
      if (fixationData.keylog.length || blank1Data.keylog.length > 1 || doubleTap || noResponse) {
        autolose = true;
      }
      
      const pointVal = cueData.point_value.toFixed(2); 

      let message = targetData.hit ? "WIN!" : "LOSE!";
      let mark = targetData.hit ? checkMark : xMark;
      if (autolose) {
        message = "LOSE!";
        mark = xMark;
      }
      let feedback = condition.type === 'points' ? `pts ` : `$`;
      feedback += pointVal + ' ';
      feedback += mark;

      return (
        `<div class="feedback">` +
          `<div class="feedback-message">${message}</div>` +
          `<div class="feedback-points">${feedback}</div>` +
        `</div>`
      )
    },
    data: { feedback: true },
    response_ends_trial: false,
    on_start: ({data}) => {
      data.start_time_elapsed = jsPsych.totalTime();
    },
    on_finish: (data) => {
      const timelineData = jsPsych.data.getLastTimelineData();
      const fixationData = JSON.parse(
        timelineData.filter({fixation: true}).json()
      ).pop();
      const targetData = JSON.parse(
        timelineData.filter({target: true}).json()
      ).pop();
      const blank1Data = JSON.parse(
        timelineData.filter({blank1: true}).json()
      ).pop();

      // DETECT CHEATING
      let autolose = false;
      const noResponse = (!fixationData.rt && !targetData.rt && !blank1Data.rt)
      const doubleTap = (targetData.rt && blank1Data.rt);
      if (fixationData.keylog.length || blank1Data.keylog.length > 1 || doubleTap || noResponse) {
        autolose = true;
      }

      data.autolose = autolose;
      
      if (!autolose) {
        staircase.addResponse(targetData.hit);
      }
      
    },
    trial_duration: () => trialTimes.getCurrentTrial('feedback')
  }
  return feedback;
}

function getBlank2(trialTimes, pointsTracker) {
  const blank2 = {
    type: "html-keyboard-response",
    stimulus: '<div></div>',
    response_ends_trial: false,
    data: {blank2: true},
    trial_duration: () => trialTimes.getCurrentTrial('blank2'),
    on_start: ({data}) => {
      data.start_time_elapsed = jsPsych.totalTime();
    },
    on_finish: function(data) {
      data.presentation_duration = this.trial_duration;

      const timelineData = jsPsych.data.getLastTimelineData();
      const cueData = JSON.parse(
        timelineData.filter({cue: true}).json()
      ).pop();
      const fixationData = JSON.parse(
        timelineData.filter({fixation: true}).json()
      ).pop();
      const targetData = JSON.parse(
        timelineData.filter({target: true}).json()
      ).pop();
      const blank1Data = JSON.parse(
        timelineData.filter({blank1: true}).json()
      ).pop();

      // DETECT CHEATING
      if (targetData.hit && 
        !(fixationData.keylog.length || blank1Data.keylog.length >= 2)) {
          pointsTracker.addValue(cueData.point_value);
      }
      pointsTracker.incrementIndex();
      trialTimes.incrementTrialIndex();
      
    }
  };
  return blank2;
}

function getInitFullscreen() {
  const fullscreen = {
    type: 'fullscreen',
    data: { init_fullscreen: true},
    message: () => { return (
      `<p>The experiment will now go fullscreen.</p>` +
      `<p>Click "Begin" when you are ready.</p>`
    )},
    button_label: "Begin",
    on_finish: (data) => {
      const elem = document.getElementById('jspsych-experiment');
      elem.classList.add("fullscreen");
      elem.focus();
    }
  }
  return fullscreen;
}

function getEndFullscreen() {
  return {
    type: 'fullscreen',
    fullscreen_mode: false
  };
}

export {
  instructionsCopy,
  getTrialInstructions,
  getCue,
  getFixation,
  getTarget,
  getBlank1,
  getFeedback,
  getBlank2,
  getInitFullscreen,
  getEndFullscreen
}
